.modal__login {
  display: none;
  top: 120px;
  left: 50%;
  width: 300px;
  margin-left: -230px;
  padding: 50px 80px; }

.modal__close {
  position: absolute;
  top: 0;
  right: -34px;
  width: 22px;
  height: 22px;
  font-size: 0;
  background-color: transparent;
  border: none;
  cursor: pointer; }
  .modal__close::before, .modal__close::after {
    content: "";
    position: absolute;
    top: 10px;
    left: 2px;
    width: 19px;
    height: 3px;
    background-color: #d0d0d0; }
  .modal__close::before {
    transform: rotate(45deg); }
  .modal__close::after {
    transform: rotate(-45deg); }

