.modal__map {
  display: none;
  top: 50%;
  left: 50%;
  width: 766px;
  height: 561px;
  margin-top: -280px;
  margin-left: -390px;
  border: 7px solid #fff; }

.modal__map p {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  margin: 0; }

.modal__map iframe {
  position: relative;
  z-index: 2;
  border: none; }

.modal__show {
  display: block;
  animation: bounce 0.6s; }

.modal__error {
  animation: shake 0.6s; }

@keyframes bounce {
  0% {
    transform: translateY(-2000px); }
  70% {
    transform: translateY(30px); }
  90% {
    transform: translateY(-10px); }
  100% {
    transform: translateY(0); } }

@keyframes shake {
  0%,
  100% {
    transform: translateX(0); }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-10px); }
  20%,
  40%,
  60%,
  80% {
    transform: translateX(10px); } }

