.catalog__item h3 {
  order: 1;
  margin: 17px 15px 12px 16px;
  font-size: 14px;
  line-height: 18px; }

.catalog__item .btn {
  line-height: 20px;
  color: #fff; }

.catalog__category {
  display: block;
  text-transform: none; }

.catalog__item-image {
  width: 220px;
  height: 165px;
  margin: 0; }

.catalog__item-price {
  display: flex;
  margin: 15px;
  margin-top: 0; }

.catalog__item-price b {
  width: 112px;
  padding: 13px 25px 11px; }

.catalog__item-price .btn {
  width: 50px;
  padding: 13px 18px 11px;
  margin-right: 0; }

.paginator {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.paginator__item {
  margin-right: 5px; }
  .paginator__item:last-child {
    margin-right: 0; }

.paginator__item a {
  display: block;
  padding: 10px 19px;
  color: #fff;
  background-color: #000; }
  .paginator__item a:hover, .paginator__item a:focus, .paginator__item a:active {
    background-color: #663d15; }

.paginator__item--current a {
  display: block;
  margin-right: 5px;
  padding: 10px 19px;
  color: #000;
  background-color: #fff; }

.main-header {
  margin-bottom: 75px;
  background-color: transparent; }

.inner-page .main-header {
  background-color: #000; }

.main-header__nav {
  display: flex;
  flex-direction: column;
  align-items: center; }

.inner-page .main-header__nav {
  align-items: flex-start;
  flex-direction: row; }

.main-header__logo {
  order: 2;
  width: 368px;
  height: 153px; }

.inner-page .main-header__logo {
  order: 0;
  width: 111px;
  height: 24px;
  padding: 23px 20px; }

.contacts {
  width: 380px; }

.contacts p {
  margin: 0;
  margin-bottom: 25px; }

.user-navigation {
  display: flex;
  max-width: 140px; }

.user-navigation a {
  position: relative;
  padding-left: 50px; }
  .user-navigation a::before {
    content: "";
    position: absolute;
    top: 27px;
    left: 24px;
    width: 16px;
    height: 18px;
    background-image: url("../img/login.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    opacity: 0.3; }
  .user-navigation a:hover::before, .user-navigation a:focus::before, .user-navigation a:active::before {
    opacity: 1; }
